.menu {
  &-btn {
    position: absolute;
    top: 0;
    right: 0;
    padding: 2em;
    border-radius: 100%;
    transition: .3s ease;
    cursor: pointer;
    font-size: -5em;
    z-index: 2;

    svg {
      position: fixed;
      right: 0;
      top: 0;
      margin: 3em;
      width: 4em;
      transition: .5s ease; }
    svg:hover {
      transform: rotate(360deg); } } }

.menu-circle {
  position: fixed;
  top: 0;
  right: 0;
  padding: 1em;
  margin: 3em;
  width: 2em;
  height: 2em;
  border-radius: 100%;
  transition: .5s ease;
  background-color: white;
  color: white;
  opacity: 0.2;
  &-click {
    background: none;
    width: 100%;
    height: 100%;
    transform: scale(30); } }

.menu-screen {
  opacity: 0;
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  transition: 1s ease;
  top: 0;
  &-click {
    z-index: 1;
    opacity: 0.9;
    background-color: white; } }

nav {

  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  text-transform: uppercase;
  width: 7em;
  font-size: 2.5em;
  font-weight: 300;
  li {
    list-style: none;
    padding-top: 1.2em; }
  ul {
    padding: 0; } }

@media screen and (max-width: 1024px) {
  .menu-btn svg {
    margin: 1.5em;
    width: 3.5em; }

  .menu-circle {
    margin: 1.5em;
    width: 1.5em;
    height: 1.5em; }

  nav {
    font-size: 2em; } }
