
body {
  background-color: #e8e8e8; }

.content, .content-large {
  margin: 0 auto;
  width: 1366px;

  &-large {
    width: 1666px; }

  &-full {
    width: 100%; } }

.progress-bars {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%; }

.progress {
  width: 10em;
  margin: 1em; }
.CircularProgressbar-text {
  fill: #616161 !important; }

.project {
  &-item {
    font-size: 1.2em;
    line-height: 2em;
    font-family: 'Montserrat', sans-serif;
    padding: 1em;
    height: 20em;
    color: white; }
  &-column {
    width: 50%;
    float: left;
    position: relative;
    height: 100%; }

  &-text {

    width: 700px;
    font-size: 1.2em;
    color: white;
    text-align: left !important; }

  &-cell {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 50%;
    transform: translateY(-50%);
    text-align: center; }

  &-name {
    h2 {
      margin-top: 0; } }
  &-image {
    img {
      width: 360px; } } }

.page-group {
  width: 65em;
  margin: 0 auto;
  margin-top: 1.5em; }

.title-section {
  background-color: #4e4390;
  width: 100%;
  font-weight: 300;
  color: white;
  padding: .5em;
  box-sizing: border-box; }

footer {
  background-color: #190035;
  width: 100%;
  color: white;
  font-size: 1em;
  ul {
    margin: 0;
    padding: 0; }
  li {
    list-style: none;
    display: inline-block;
    padding-left: .5em;
    padding-right: .5em;
    padding-top: 1.5em;
    padding-bottom: 1.5em;
    text-transform: capitalize; } }

.center-text {
  text-align: center; }

.footer-img {
  position: relative;
  bottom: -.5em;
  width: 50em;

  z-index: -1; }

@media screen and (max-width: 1366px) {
  .content, .content-large, .page-group {
    width: 100%; }
  .header-text {
    font-size: 1.2em; }
  .project-text {
    width: 300px;
    font-size: .9em;
    line-height: 1.8em; }
  .project-image img {
    width: 260px; }
  .progress {
    width: 8em; } }

@media screen and (max-width: 768px) {
  .footer-img {
    width: 80%; } }

@media screen and (max-width: 414px) {
  .project-column {
    width: 100%;
    float: none;
    height: 15em; }
  .project-item {
    padding-top: 3em;
    padding-bottom: 0em;
    height: 100%; }

  footer {
    font-size: .9em; } }
