header {
  overflow: hidden; }

.cover {
  width: 100%;
  height: calc(100vh);
  position: relative;
  background-size: cover; }

.header-text {
  position: absolute;
  left: 0;
  right: 0;
  color: white;
  font-size: 1.5em;
  margin: 0 auto;
  top: 44%;
  width: 25.5em;
  font-family: 'Montserrat', sans-serif;
  text-align: center; }
h1 {
  font-weight: 400;
  margin: 0;
  span {
    font-weight: 600; } }
h3 {
  margin-top: .5em;
  font-weight: 400;
  font-size: 1.5em; }

@media screen and (max-width: 1366px) {

  .header-text {
    font-size: 1.2em; } }
@media screen and (max-width: 414px) {
  .header-text {
    font-size: .8em; } }
